<template>
<div class="uk-width-medium-3-4">
        <div class="uk-grid-margin" >
            <div class="uk-width-1-1">
                <article class="uk-article">
                <h2 v-text="blog.title"></h2>
                <p class="uk-article-meta">发表于<span v-text="blog.create_time|smartDate"></span></p>
                <p><span v-html="blog.content"></span></p>
                </article>
                <a v-show="blog.user_id === user.id" v-link="'/edit/'+blog.id">编辑</a><br/><br/>
            </div>
        </div>
        <div class="uk-container uk-padding-remove" id="vm">
            <div class="uk-width-1-1 uk-margin-large-top">

                <article v-show="user" class="uk-comment">
                    <header class="uk-comment-header">
                        <img class="uk-comment-avatar uk-border-circle" width="50" height="50" :src="user.image">
                        <h4 class="uk-comment-title" v-text="user.nickname"></h4>
                    </header>
                    <div class="uk-comment-body">
                        <form id="form-comment" class="uk-form" v-on:submit.prevent="submit">
                            <div class="uk-alert uk-alert-danger uk-hidden"></div>
                            <div class="uk-form-row">
                                <textarea rows="6" placeholder="说点什么吧" style="width:100%;resize:none;" v-model="comment"></textarea>
                            </div>
                            <div class="uk-form-row">
                                <button type="submit" class="uk-button uk-button-primary"><i class="uk-icon-comment"></i> 发表评论</button>
                            </div>
                        </form>
                    </div>
                </article>

                <hr class="uk-article-divider">
                <h3>最新评论</h3>

                <ul class="uk-comment-list">

                    <li v-if="comments.length > 0">
                        <article class="uk-comment" v-for="comment in comments">
                            <header class="uk-comment-header">
                                <a :href="'/home/user/'+comment.user.id"><img class="uk-comment-avatar uk-border-circle" width="50" height="50" :src="comment.user.image"></a>
                                <h4 class="uk-comment-title"><span v-text="comment.user.nickname"></span><span v-if="comment.user.id === blog.user_id">(作者)</span></h4>
                                <p class="uk-comment-meta"><span v-text="comment.create_time|smartDate"></span></p>
                            </header>
                            <div class="uk-comment-body">
                                <span v-text="comment.content"></span>
                            </div>
                        </article>
                        
                    </li>

                    <p v-else>还没有人评论...</p>

                </ul>
            </div>
            <pagination v-bind:page="page"></pagination>
        </div>
    </div>

    <div class="uk-width-medium-1-4">
        <div class="uk-panel uk-panel-box">
            <a><div class="uk-text-center">
                <img class="uk-border-circle" width="120" height="120" :src="blog.user.image">
                <h3 v-text="blog.user.nickname"></h3>
            </div></a>
        </div>
    </div>
</template>

<script>
import Pagination from './Pagination'
import { getUser } from '../vuex/getters'
export default {
  vuex: {
    getters: {
      user: getUser
    }
  },
  components: {
    Pagination
  },
  ready () {
    this.$http.get('/api/blog', {
      params: {
        id: this.$route.params.id
      }
    }).then(function (res) {
      this.blog = res.data.blog
    }).then(function (err) {
      console.log(err)
    })
    this.getItemsByPage(this.getUrlParams('page'), this.getUrlParams('size'))
  },
  methods: {
    submit: function () {
      this.$http.post('/api/comment_save', {
        id: this.$route.params.id,
        content: this.comment,
        token: document.cookie.split('=')[1]
      }).then(function (res) {
        this.comment = ''
        this.comments = res.data.comment.concat(this.comments)
      }).then(function (err) {
        console.log(err)
      })
    },
    getItemsByPage: function (page, size) {
      this.$http.get('/api/comments', {
        params: {
          id: this.$route.params.id,
          page: page || '1',
          size: size || '5'
        }
      }).then(function (res) {
        this.comments = res.data.comments
        this.page = res.data.page
      }).then(function (err) {
        console.log(err)
      })
    },
    getUrlParams: function (key) {
      let ret = window.location.search.match(new RegExp('(\\?|&)' + key + '=(.*?)(&|$)'))
      return ret && decodeURIComponent(ret[2])
    }
  },
  data () {
    return {
      blog: '',
      comments: '',
      page: '',
      comment: ''
    }
  }
}
</script>



